@import "../../global.scss";

.topbar{
    width: 100%;
    height: 65px;
    background-color: white;
    transition: all 1s ease;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    scrollbar-width: none; 
            &::-webkit-scrollbar{
                display: none;}
    

.wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;


.left{
    display: flex;
    align-items: center;

    .logo{
        font-size: 45px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
        
    }

    .itemContainer{
        display: flex;
        align-items: center;
        margin-left: 30px;

        @include mobile{
          display: none;
        }
        

        .icon{
            font-size: 20px;
            margin-right: 5px;
        }

        span{
            font-size: 16px;
            font-weight: 600;
            
        }
    }
}
        .right{

            .hamburger{
                width: 30px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                

                span{
                    width: 100%;
                    height: 4px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                    
                } 
              }
            }
        }
        &.active {
            background-color: $mainColor;
            color: white;
        
            .hamburger {
              span {
                &:first-child {
                  background-color: white;
                  transform: rotate(45deg);
                }
                &:nth-child(2) {
                  opacity: 0;
                }
                &:last-child {
                  background-color: white;
                  transform: rotate(-45deg);
                }
              }
            }
          }
        }