@import "../../global.scss";

.intro{
  background-color: whitesmoke;
  display: flex;

@include mobile{
  flex-direction: column;
  align-items: center;
}

  .left{
    flex: 0.5;
    overflow: hidden;

    .imgContainer{
      width: 700px;
      height: 700px;
      display: flex;
      background-color: rgb(145, 193, 209);
      border-radius: 50%;
      align-items: flex-end;
      justify-content: center;
      float: right;
     
     @include mobile{
       align-items: flex-start;
       
     }

      img{
        height: 98%;
        width: 95%;
        border-radius: 50%;
      
        @include mobile{
          height: 50%;
          width: 50%;
        }
      }
    }
  }
  .right{
    flex: 0.5;
    position: relative;

    .wrapper{
      width: 100;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
      }

      h1{
        font-size: 60px;
        margin: 10px 0;
        color: rgb(145, 193, 209);

        @include mobile{
          font-size: 40px;
        }
      }
      h2{
        font-size: 35px;
        color: rgb(145, 193, 209);
      }
      h3{
        font-size: 30px;
        @include mobile{
          font-size: 20px;
        }

        span{
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor{
          animation: blink 1s infinite;
        }

        @keyframes blink{
          50%{
            opacity: 1;
          }
          100%{
            opacity: 0;
          }
        }
      }
    }
    a{
      position: absolute;
      bottom: 40px;
      left: 20%;

      img{
        width: 35px;
        animation: arrowBlink 2s infinite;
      }
      @keyframes arrowBlink {
        100%{
          opacity: 0;
        }
      }
    }
  }
}